import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';

export default {
	components:{
		uniNavBar
	},
	data() {
		return {
			content:'',
			imageList:[],
			showBack:false
		}
	},
	onBackPress() {
		console.log(11);
		if((this.content !== '' || this.imageList.length>0 ) && !this.showBack){
			uni.showModal({
				content: '是否要保存草稿',
				showCancel: true,
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if(res.confirm){
						console.log('保存')
					}
					uni.navigateBack({delta: 1});
					
				}
			});
			this.showBack = true;
			return true
		}
	}
}
